'use client'
import React from "react";


import Link from "next/link";
import { FaPlayCircle } from "react-icons/fa";
import {images} from "@/constants/index"
import Image from "next/image";
import { ArrowUpRight } from "lucide-react";

const Hero = () => {
  return (
    <div className="flex font-header flex-col  px-4 text-white text-start  mt-[190px] space-y-5">
      {/* Heading */}
      <div className="">
        <h2 className="md:text-[66px] text-[28px] ml-[82px] md:leading-[99px] font-extrabold">
          Travel for Life
        </h2>
        <p className="md:text-[18px] text-[14px] leading-[27px] w-[275px] mt-[20px] ml-[81px] font-normal">
          Book your desired travel place
        </p>
        <div className="flex space-x-6">
        <button className="mt-[50px]  bg-[#FF5C00] ml-[82px]  w-full md:w-[179px] h-[43px] px-4 py-2 rounded-full font-extrabold text-[16px] leading-[24px] hover:bg-black hover:text-white transition">
              Book a trip
            </button>
            <button className="mt-[50px]  border  border-white w-full md:w-[179px] h-[43px] px-4 py-2 rounded-full font-extrabold text-[15px] hover:bg-black hover:border-transparent hover:text-white transition">
              Learn more
            </button>
            <div className="mt-[50px]   w-full md:w-[179px] h-[43px] px-4  rounded-full font-extrabold text-[15px] cursor-pointer hover:border-transparent hover:text-white transition">
            <FaPlayCircle className="inline size-[41px] text-white mr-2" />
              Watch Intro
            </div>
        </div>

        
      </div>

      <div className="relative w-full h-screen flex flex-col justify-between text-white">
            {/* Numbered Progress Bar */}
      <div className="absolute top-8 left-36 ml-[82px] mt-[40px] transform -translate-x-1/2 flex items-center space-x-6 text-white text-sm">
        <span className="opacity-80">01</span>
        <div className="w-32 border-b-2 border-white"></div>
        <span className="opacity-80">02</span>
        
        <span className="opacity-80">03</span>
       
        <span className="opacity-80">04</span>
      </div>

      {/* Text Section at Bottom Left */}
      <div className="absolute bottom-16 flex justify-between  w-[1030px]">
        <h1 className="text-[50px] md:text-[68px] ml-[82px] font-bold leading-[77px]">
          Explore top <br /> destination
        </h1>
        <div className="flex flex-col ml-[50px] justify-between w-[506px]">
          <p className="text-[18px] leading-[27px] font-normal  text-gray-200 mt-4">
          Explore your suitable and dream places around the world. Here you can find your right destination.
        </p>

        {/* Learn More Button */}
        <button className="mt-6 px-6 py-3 w-[271px] h-[53px] font-bold border border-white text-white text-[16px] leading-[24px] rounded-full hover:bg-white hover:text-black transition-all">
          Learn More
        </button>

        </div>
      
        
      </div>
      
    
      </div>
      
      <div className="relative flex w-full mt-8 justify-center space-x-6">
      {images.map((image, index) => (
         <div key={index} className="relative" >
         <Image
           src={image.src}
           alt={`Image ${index + 1}`}
           width={300}
           height={300}
            className={`rounded-2xl object-cover h-[413px] `} 
            style={{ width: `${image.width}px` }} 
         />

          {/* Floating Arrow Button (on middle image) */}
          {index === 1 && (
            <button className="absolute bottom-8 right-8 bg-[#FF5C00] size-[94px] text-white p-4 rounded-full shadow-lg hover:bg-orange-600 transition">
              <ArrowUpRight className="size-[55px]" size={50} />
            </button>
          )}
        </div>
      ))}
    </div>
      

      
    </div>
  );
};

export default Hero;
